body {
	line-height: 1.2;
	font-size: 16px;
	font-family: roboto;
}
h1 {
	font-family: OS_B;
	font-size: 35px;
	color: $blue;
	margin: 40px 0 25px;
}
h2 {
	font-size: 1.6rem;
	margin: 20px 0 15px;
}
h3 {
	font-size: 1.3rem;
	margin: 15px 0 10px;
}
h4 {
	font-size: 1.2rem;
	margin: 15px 0 10px;
	font-weight: 700;
}
header {
	height: 50px;
	background-color: blue;
	position: relative;
	nav {
		&>ul {
			width: 90%;
			align-items: center;
			height: 50px;
			list-style-type: none;
			font-family: OS_I;
			@include flex(space-evenly, center);
			&>li {
				font-size: 20px;
				cursor: pointer;
				color: white;
				&:not(:last-child) {
					margin-right: 15px;
				}
				&:hover {
					color: $red;
				}
			}
		}
	}
}
.content_page {
	padding: 50px 10%;
}
code {
	background-color: #e0dbdb;
	display: inline-block;
	padding: 10px 15px;
}
span {
	margin-left: 20px;
}
.file_form {
	width: 80%;
	margin: auto;
	text-align: center;
	form {
		text-align: left;
	}
	label {
		display: block;
		margin-top: 30px;
	}
	input, select, textarea {
		width: 100%;
	}
	span.red {
		margin-left: 5px;
		color: red;
	}
}
.active {
	position: relative;
	&:after {
		content: '';
		position: absolute;
		background-color: #fff;
		height: 3px;
		width: 100%;
		top: 100%;
    	left: 0;
	}
}